.row{display:flex}.row section{border-radius:10px;width:calc(50% - 40px);padding:20px;position:relative;box-shadow:0 4px 10px 0 rgba(128,128,128,.5);transition:.3s all linear}.row section:hover{box-shadow:0 4px 10px 0 gray}.row section::before{position:absolute;content:"";height:15px;width:15px;background:#f7f7f7;top:28px;z-index:-1;transform:rotate(45deg)}.row section .icon{position:absolute;background:#f2f2f2;height:43px;width:43px;padding:5px;text-align:center;line-height:40px;border-radius:50%;color:#161b22;font-size:17px;box-shadow:0 0 0 4px #f7f7f7,inset 0 2px 0 rgba(0,0,0,.08),0 3px 0 4px rgba(0,0,0,.05)}.row section .details .title{font-size:24px;font-weight:500;color:#333;font-family:"Ubuntu",sans-serif}.row section .details .subtitle{font-style:italic}.row section .details .date{color:#161b22;font-size:18px;font-family:"JoseFin sans",sans-serif}.row section .details p{margin:0}.row section .buttons{margin:30px 0 10px 0}.row section .buttons .hosted{text-decoration:none;font-size:1rem;color:#000;border-bottom:2px solid #b8b8b8}.row section ul{list-style:none;margin-left:0;padding-left:0}.row section ul li{padding-left:1em;text-indent:-1em}.row section ul li::before{content:"◉";padding-right:5px}.row section p,.row section li{margin:10px 0;font-size:18px;line-height:1.4;letter-spacing:.2px;color:#72777e}.row_1{justify-content:flex-start}.row_1 section::before{right:-7px}.row_1 section .icon{top:15px;right:-60px}.row_2{justify-content:flex-end}.row_2 section::before{left:-7px}.row_2 section .icon{top:15px;left:-60px}@media(max-width: 790px){.row{margin:30px 0 3px 60px}.row section{width:100%}.row_1 section::before{left:-7px}.row_1 section .icon{left:-60px}}@media(max-width: 440px){.row{margin:10px 0}.row section::before,.row section .icon{display:none}}